import { PaneWidths } from "../filesPage.types"

type GetFilesPaneWidthsProps = {
  isChatPaneOpen: boolean
  isDocumentPaneExpanded: boolean
  isDocumentPaneOpen: boolean
}

function getFilesPaneWidths({
  isChatPaneOpen,
  isDocumentPaneExpanded,
  isDocumentPaneOpen,
}: GetFilesPaneWidthsProps): PaneWidths {
  if (isDocumentPaneExpanded) {
    return { filesPaneWidth: null, chatPaneWidth: null, documentPaneWidth: "100%" }
  }

  if (isDocumentPaneOpen && isChatPaneOpen) {
    return { filesPaneWidth: "34%", chatPaneWidth: "33%", documentPaneWidth: "33%" }
  }

  if (isDocumentPaneOpen) {
    return { filesPaneWidth: "50%", chatPaneWidth: null, documentPaneWidth: "50%" }
  }

  if (isChatPaneOpen) {
    return { filesPaneWidth: "50%", chatPaneWidth: "50%", documentPaneWidth: null }
  }

  return { filesPaneWidth: "100%", chatPaneWidth: null, documentPaneWidth: null }
}

export { getFilesPaneWidths }
